.pagination{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    cursor: pointer;
}

.pagination-button, .pagination-end-button{
    background-color: #efefef;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 5px 10px;
    font-weight: bolder;
    font-size: 1em;
    margin: 0 5px;
}